import React from "react"
import Layout from "../../components/Layout"
import { graphql, Link } from "gatsby"
import { formatName } from "../../js/scripts"
import * as projectListStyles from "../../styles/projectlist.module.css"

const Module = ({ data }) => {
  let location
  if (typeof window !== "undefined") {
    location = window.location.href
    let currentUrl = location.split(/\//)[4]
    let usableData = data.allDirectory.edges.slice(5)
    return (
      <Layout>
        <div className={projectListStyles.titleDiv}>
          <h3>{formatName(currentUrl)}</h3>
        </div>
        <div className={projectListStyles.listDiv}>
          <ul className={projectListStyles.listUl}>
            {
              usableData.map(item => item.node.relativeDirectory === currentUrl && <li key={item.node.id} ><Link to={`/${item.node.relativeDirectory}/${item.node.name}`} >{item.node.name}</Link></li>)
            }
          </ul>
        </div>
      </Layout>
    )
  } else {
    return null
  }
}

export default Module

export const modProjs = graphql`
    query modProjs($relativePath: String) {
        allDirectory(filter: {name: {eq: $relativePath}}) {
            edges {
              node {
                name
                relativeDirectory
                relativePath
                id
              }
            }
          }
    }
    `